<template>
  <div class="download container text-left mt-5">
    <h1>Цифровой искусствовед</h1>
    <div class="mb-5">Загрузите изображение, узнайте когда была написана картина</div>
    <vue-dropzone ref="myVueDropzone" id="dropzone" :options="dropzoneOptions" v-on:vdropzone-complete="onComplete"></vue-dropzone>
    <h2 class="mt-5 mb-3">Предсказания модели</h2>
    <div class="result">
      <div v-for="res in result" class="res mb-3">
          <div class="conf">Точность: {{res.conf}}</div>
          <div class="year">Год написания: {{res.date}}</div>
      </div>
    </div>
  </div>
</template>

<script>
  import vue2Dropzone from 'vue2-dropzone'
  import 'vue2-dropzone/dist/vue2Dropzone.min.css'

  export default {
    name: 'Download',
    components: {
      vueDropzone: vue2Dropzone
    },
    data () {
      return {
        dropzoneOptions: {
          url: 'https://date-q2wlyryiha-uc.a.run.app',
          paramName: 'image'
        },
        result: []
      }
    },
    methods: {
      onComplete(response) {
          this.result = [];
          var result = JSON.parse(response.xhr.response);

          if(result.result.length) {
            this.result.push(result.result[0]);
            this.result.push(result.result[1]);
          }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @font-face {
    font-family: "BreeSerif";
    src: local("BreeSerif"),
    url(/fonts/BreeSerif-Regular.ttf) format("truetype");
  }
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  .vue-dropzone {
    border: 1px solid #e5e5e5;
    box-shadow: 0 0 8px 2px rgba(0,0,0,0.1);
  }
  .title {
    font-family: "BreeSerif";
    font-weight: bold;
    font-size: 50px;
    line-height: 50px;
    color: #a66403
  }
  .title:nth-child(2) {
    margin-left: 120px;
  }
  .title.app-name {
    font-size: 80px;
    line-height: 80px;
  }
  .result .res div {
    padding: 5px 15px;
    border: 1px solid #cccccc;
    border-bottom: none;
  }
  .result .res div:last-child {
    border-bottom: 1px solid #cccccc;
  }
</style>



