import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Download from '@/components/Download'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/download',
      name: 'Download',
      component: Download
    }
  ]
})



